import Vue from 'vue'
import Router from 'vue-router'
import routes from './routes'

Vue.use(Router)

let router = new Router({
  routes: routes
})

router.beforeEach((to, from, next) => {
  let autoLogin = Vue.localStore.get('autoLogin')
  if (to.path === '/login') {
    if (autoLogin) {
      sessionActive().then(() => {
        next({ path: '/' })
      }).catch(() => {
        next()
      })
    } else {
      next()
    }
  } else {
    sessionActive().then(() => {
      next()
    }).catch(() => {
      next({ path: '/login' })
    })
  }
})

function sessionActive () {
  return new Promise((resolve, reject) => {
    Vue.http.get('/ping').then((res) => {
      if (res && res.data) {
        resolve()
      } else {
        reject(new Error())
      }
    }).catch((err) => {
      reject(err)
    })
  })
}

export default router



