const rushJob = (resolve) => { require.ensure([], () => resolve(require('@/modules/rush-job/pages/RushJob.vue')), 'todo') }

const route = [
  { name: 'rush-job',
    path: '/my/rush-job',
    component: rushJob
  }
]

export default route



