import Vue from 'vue'

const sprint = {
  state: {
    bugList: [],
    curSprintId: 0,
    filterText: '',
    bugOrbit: [],
    isTodo: 'todo'
  },

  // 时候我们需要从 store 中的 state 中派生出一些状态，例如对列表进行过滤并计数
  getters: {
    getBug: (state) => (id) => {
      return state.bugList.find(item => item.id === id)
    },
    filterBugList: (state) => {
      return state.bugList.filter(item => {
        return (item.sprintId === state.curSprintId) && ((item.name.indexOf(state.filterText) !== -1) || (item.id.indexOf(state.filterText) !== -1))
      })
    }
  },

  /**
   * 更改 Vuex 的 store 中的状态的唯一方法是提交 mutation
   * 一条重要的原则就是要记住 mutation 必须是同步函数
   */
  mutations: {
    setBugList (state, payload) {
      state.bugList = payload
    },
    setFilterText (state, payload) {
      state.filterText = payload
    },
    addBug (state, payload) {
      state.bugList.push(payload)
    },
    updateBug (state, payload) {
      for (let i = 0, len = state.bugList.length; i < len; i++) {
        if (state.bugList[i]._id === payload._id) {
          state.bugList[i] = payload
        }
      }
    },
    deleteBug (state, payload) {
      state.bugList = state.bugList.filter(item => item._id !== payload)
    },
    setCurSprintId (state, payload) {
      state.curSprintId = payload
    },
    setBugOrbit (state, payload) {
      state.bugOrbit = payload
    },
    addBugOrbit (state, payload) {
      state.bugOrbit.push(payload)
    },
    setIsTodo (state, payload) {
      state.isTodo = payload
    }
  },

  actions: {
    findBugList ({ dispatch, commit, state }) {
      return new Promise((resolve, reject) => {
        if (state.isTodo === 'todo') {
          dispatch('findTodoBug').then((data) => {
            commit('setBugList', data)
            resolve()
          })
        } else {
          dispatch('findDoneBug').then((data) => {
            commit('setBugList', data)
            resolve()
          })
        }
      })
    },
    findTodoBug ({commit, state, rootState}) {
      let condition = {
        curUserId: rootState.user.email,
        state: { '$ne': 5 }
      }
      return new Promise((resolve, reject) => {
        Vue.dataService.find(Vue.apiUrl.BASE_FIND + 'bugs', condition).then((data) => {
          resolve(data)
        })
      })
    },
    findDoneBug ({commit, state, rootState}) {
      // 查询三月内
      let t = new Date()
      t.setMonth(t.getMonth() - 3)
      let condition = {
        optUserId: rootState.user.email,
        optTime: { '$gt': t }
      }
      return new Promise((resolve, reject) => {
        Vue.dataService.find(Vue.apiUrl.BASE_FIND + 'bugOrbit', condition).then((orbit) => {
          let bugIds = []
          for (let i = 0, len = orbit.length; i < len; i++) {
            if (bugIds.indexOf(orbit[i].bugId) === -1) {
              bugIds.push(orbit[i].bugId)
            }
          }
          condition = {
            id: { '$in': bugIds },
            '$or': [
              { state: 5 },
              { curUserId: { '$ne': rootState.user.email } }
            ]
          }
          Vue.dataService.find(Vue.apiUrl.BASE_FIND + 'bugs', condition).then((data) => {
            resolve(data)
          })
        })
      })
    },
    findBugOrbit ({commit, state}, id) {
      let condition = {
        bugId: id
      }
      return new Promise((resolve, reject) => {
        Vue.dataService.find(Vue.apiUrl.BASE_FIND + 'bugOrbit', condition).then((data) => {
          commit('setBugOrbit', data)
          resolve(data)
        })
      })
    },
    findBugById ({commit, state}, id) {
      let condition = {
        id: id
      }
      return new Promise((resolve, reject) => {
        Vue.dataService.find(Vue.apiUrl.BASE_FIND + 'bugs', condition).then((data) => {
          if (data && (data.length === 1)) {
            resolve(data[0])
          }
        })
      })
    },
    saveBugOrbit ({dispatch, commit, state, rootState}, bug) {
      let data = {
        bugId: bug.id,
        toUserId: bug.curUserId,
        optTime: new Date(),
        optUserId: rootState.user.email,
        state: bug.state
      }
      if (state.bugOrbit.length > 0) {
        let last = state.bugOrbit[state.bugOrbit.length - 1]
        if ((last.state === data.state) && (last.toUserId === data.toUserId)) {
          // 状态未变动
          return
        } else {
          data.fromUserId = last.toUserId
          data.stayTime = Vue.utils.subDate(data.optTime, new Date(last.optTime))
        }
      }
      Vue.dataService.save(Vue.apiUrl.BASE_SAVE + 'bugOrbit', data).then((data) => {
        commit('addBugOrbit', data)
        dispatch('findBugList')
      })
    }
  }
}

export default sprint



